<template>
  <div class="pdct-l-wp layout" v-if="tabs.length">
    <TheCompiler :tabs='tabs' @changeTab='handleChangeTab'></TheCompiler>
    <TheContentList :message='message' @toDetail='handleToDetail'></TheContentList>
  </div>
</template>

<script>
import TheCompiler from '../components/product/list/TheCompiler.vue'
import TheContentList from '../components/product/list/TheContentList'

export default {
  data () {
    return {
      tabs: [],
      message: []
    }
  },
  methods: {
    handleChangeTab (item) {
      this.$router.push('/37duc/product/list?type=' + item.id)
    },
    handleToDetail (id) {
      this.$router.push({
        path: '/37duc/product/detail/' + id,
        query: {
          productId: id
        }
      })
    },
    getMessage (productCategoryId, page, limit) {
      return new Promise((resolve, reject) => {
        this.$api.product.getProductListWidthType({
          productCategoryId: productCategoryId,
          page: page,
          limit: limit
        }).then(data => resolve(data))
      })
    },
    renderList (data) {
      let message = []
      data.forEach(val => {
        let item = {}
        item.name = val.name
        item.marks = [val.feature1, val.feature2, val.feature3]
        item.price = val.price
        item.character = []
        val.parameter_values[0].entries.forEach(entry => item.character.push({ ...entry }))
        item.insure_condition = val.insure_condition
        item.id = val.id
        message.push(item)
      })
      this.message = message
    },
    init () {
      let type = this.$route.query.type
      if (!this.tabs.find(tab => tab.id == type)) {
        this.$router.replace('/37duc/404')
        return
      }
      this.getMessage(type, 1, 6)
        .then(data => {
          this.renderList(data.data)
        })
      this.tabs.find(tab => tab.id != type && tab.active).active = false
      this.tabs.find(tab => tab.id == type).active = true
    }
  },
  created () {
    this.$api.product.getProductType()
      .then((data) => {
        data.forEach((v, k) => this.tabs.push({
          tabTxt: v.name,
          active: k == 0,
          id: v.id
        }))
        if (!this.tabs.length) { this.$router.replace('/37duc/404'); return }
        if (this.$route.query.type === undefined) { this.$router.replace('/37duc/product/list?type=' + this.tabs[0].id); return }
        this.init()
      })
  },
  watch: {
    '$route': {
      handler (val) {
        this.init()
      }
    }
  },
  components: {
    TheCompiler,
    TheContentList
  }
}
</script>

<style lang="scss" scoped="scoped">
  .pdct-l-wp {
    wdith: 1100px;
    margin: 12px auto;
  }
</style>
